import Link from 'next/link'
import home from '../icon/home.svg'


const Header = () => {
    return (
        <dir class="container-fluid" id="bar" >
            <div class="container-fluid">
                <div class="row" >
                    <div class="col-lg-2" >
                        <Link href="/">
                            <img src={home} alt="Elderly" width="50px" position="left" /></Link>
                    </div>

                    <div class="col-lg-7">
                        <div>
                            <ul class="nav">
                                <a class="nav-link active" href="#" ><h5 id="barL">หน่วยงาน</h5></a>
                                <a class="nav-link active" href="#" ><h5 id="barL" >เรื่องน่ารู้</h5></a>
                                <a class="nav-link active" href="#"><h5 id="barL" >ติดต่อเรา</h5></a>
                            </ul>
                        </div>
                    </div>

                    <div class="col-lg-3" id="barR">
                       
                        <form  class="form-inline  my-lg-0">
                            <input class="form-control mr-sm-2" type="search" placeholder="Search" />
                            <button class="btn btn-outline-light my-2 my-sm-0" type="submit">Search</button>
                        </form>
                       
                    </div>
                </div>

            </div>

        </dir>
    )
}
export default Header

